.form {
  width: 592px;
  margin: 0;
  padding-left: 15px;
  padding-right: 15px;
}

.avatar {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 32px;
}

.uploadButton {
  width: 179px;
  background-color: #F2F2F2 ;
  border-radius: 100px;
  padding: 8px 20px;
  border: none;
  border-bottom: 4px solid #9D9D9D;
  list-style: none; /* –ø–æ–≤–Ω—ñ—Å—Ç—é —Å–∫–∏–¥–∞—î –≤—Å—ñ —Å—Ç–∏–ª—ñ, –≤–∫–ª—é—á–Ω–æ –∑ padding, margin, font —Ç–æ—â–æ */
  display: inline-block; /* –ø–æ—Ç—Ä—ñ–±–Ω–æ –≤—ñ–¥–Ω–æ–≤–∏—Ç–∏ display, –±–æ all: unset –π–æ–≥–æ —Å–∫–∏–¥–∞—î */
  cursor: pointer; /* —è–∫—â–æ —Ö–æ—á–µ—à, —â–æ–± –∫—É—Ä—Å–æ—Ä –±—É–≤ —è–∫ —É –∫–Ω–æ–ø–∫–∏ */

}

.submitButton {
  background-color: #FFDAE0;
  width: 480px;
  border-radius: 100%;
}

.error {
  color: red;
  font-size: 0.9rem;
}

.formUser {
  margin-top: 32px;
}

.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.formSide {
  width: 50%;
  padding: 48px;
  box-sizing: border-box;
  overflow-y: auto;
  padding-bottom: 80px;
}

.title {
  width: 480px;
  font-family: 'Comfortaa', sans-serif;
  text-align: center;
  font-weight: 700;
  font-size: 48px;
  line-height: 120%;
  letter-spacing: 0.01em;
}

.imageSide {
  position: fixed;
  top: 0;
  right: 0;

  width: 50vw;
  height: 100vh;

  overflow: hidden;
}

.imageWrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;

  /* üîë –∑–∞–ø–æ–±—ñ–≥–∞—î–º–æ zoom */
  transform: scale(1);
}

.image {
  width: 100%;
  height: 100%;

  object-fit: cover;
  object-position: center;

  /* üîë –∑–∞–ø–æ–±—ñ–≥–∞—î–º–æ –∑–±—ñ–ª—å—à–µ–Ω–Ω—é –ø—Ä–∏ –º–∞—Å—à—Ç–∞–±—ñ —Å—Ç–æ—Ä—ñ–Ω–∫–∏ */
  transform: scale(1);
  transform-origin: center center;
}

@media (min-width: 1920px) {
  .imageSide {
    width: 45vw;
  }

  .formSide {

  }
}

@media (max-width: 1200px) {
  .imageSide {
    width: 55vw;
  }

  .formSide {

  }
}

@media (max-width: 767px) {
  .imageSide {
    display: none;
  }

  .formSide {
    width: 100%;
    margin-right: 0;
    padding: 32px;
    padding-bottom: 80px;
  }
}
